//vuex기본 설정
import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      likes : 0,
      flag: false
    }
  },
  mutations :{
    //데이터 수정 방법을 정의
    이름변경(state){
      state.name = 'park';
    },
    나이증가(state, data){
      state.age += data
    },
    좋아요(state){
      if(state.flag == false){
        state.likes++;
        state.flag = true;
      }else{
        state.likes--
        state.flag = false;
      }
      
    },
  },
})

export default store